const getTime = () => {
  const date = new Date()
  const hours = date.getHours()
  const minutes = date.getMinutes()
  const seconds = date.getSeconds()
  return `${hours}${minutes < 10 ? ':0' : ':'}${minutes}${seconds < 10 ? ':0' : ':'}${seconds}`
}

const welcomeMessage = [
  "Welcome to the EasyDB Terminal!",
  {
    time: getTime(),
    type: "info",
    label: "Version:",
    content: "1.0.0",
  },
  {
    time: getTime(),
    type: "info",
    label: "Author:",
    content: "Yuxuan Dai",
  },
  {
    time: getTime(),
    type: "warning",
    label: "System:",
    content: "使用之前请先输入 `init` 初始化数据库。",
  },
  {
    time: getTime(),
    type: "warning",
    label: "System:",
    content: "输入 `document` 查看数据库文档。",
  },
  {
    time: getTime(),
    type: "warning",
    label: "System:",
    content: "如出现问题请可以尝试刷新页面。",
  }
];

const introduction = [
  {
    type: 'info',
    label: 'Name:',
    content: 'Yuxuan Dai'
  },
  {
    type: 'info',
    label: 'Sex:',
    content: 'Male'
  },
  {
    type: 'info',
    label: 'Age:',
    content: '23'
  },
  {
    type: 'info',
    label: 'Email:',
    content: 'blockClothSword@Gmail.com'
  }
]

export default {
  welcome: {
    description: "Welcome message with system introduction.",
    run(print) {
      let i = 0;
      return new Promise((resolve) => {
        const interval = setInterval(() => {
          print(welcomeMessage[i]);
          i++;
          if (!welcomeMessage[i]) {
            clearInterval(interval);
            resolve({
              type: "success",
              label: "Done",
              content: "Welcome to EasyDB!",
            });
          }
        }, 500);
      });
    },
  },
  intro: {
    description: 'Introducting myself again.',
    run(print) {
      let i = 0
      return new Promise(resolve => {
        const interval = setInterval(() => {
          print(introduction[i])
          i++
          if (!introduction[i]) {
            clearInterval(interval)
            resolve({ type: 'success', label: 'Done', content: 'Myself introduction is over!' })
          }
        }, 500)
      })
    }
  },
  echo: {
    description: 'Echoes input.',
    run(print, input) {
      return new Promise(resolve => {
        print({
          time: getTime(),
          label: 'Echo',
          type: 'success',
          content: input
        })
        resolve({ type: 'success', label: '', content: '' })
      })
    }
  },
  open: {
    description: 'Open a specified url in a new tab.',
    run(print, input) {
      return new Promise((resolve) => {
        if (!input) {
          resolve({ type: 'error', label: 'Error', content: 'a url is required!' })
          return
        }
        if (!input.startsWith('http')) {
          resolve({ type: 'error', label: 'Error', content: 'Please add `http` prefix!' })
          return
        }
        print({ type: 'success', label: 'Success', content: 'Opening' })

        window.open(input, '_blank')
        resolve({ type: 'success', label: 'Done', content: 'Page Opened!' })
      })
    }
  },
  blog: {
    description: 'Open my blog in a new tab.',
    run(print) {
      return new Promise((resolve) => {
        print({ type: 'success', label: 'Success', content: 'Opening' })
        window.open('https://blockcloth.cn', '_blank')
        resolve({ type: 'success', label: 'Done', content: 'blog' })
      })
    }
  },
  document: {
    description: 'Consult the database documentation.',
    run(print) {
      return new Promise((resolve) => {
        print({ type: 'success', label: 'Success', content: 'Opening' })
        window.open('https://blockcloth.cn/#/article?id=128', '_blank')
        resolve({ type: 'success', label: 'Done', content: 'document' })
      })
    }
  }
}
